import React from 'react';
import emailjs from 'emailjs-com';

// import './ContactUs.css';
// CSS especial
import '../../assets/css/eyevision.css'

export default function ContactUs() {

  function sendEmail(e) {
    e.preventDefault();

   this.setName({from_name: ""});

    emailjs.sendForm('eyevisioncwb', 'template_VmI4xsFQ', e.target, 'user_MAbKvMr4HZsk45CNuPauy')
      .then((result) => {
					console.log(result.text);
          alert("Seu e-mail foi enviado para a Eye Vision, Obrigado")
      }, (error) => {
          console.log(error.text);
      });
  }

  return (
    <form classId="myForm" className="contact-form" onSubmit={sendEmail}>
      <div className="div-contact">
        <div className="contact-section">
          <div className="inner-width-contact">
            <h1 className="text-contact">Entre em contato conosco</h1>
            <input type="text" className="name-contact" name="from_name" placeholder="Seu nome" />
            <input type="text" className="email-contact" name="from_email" placeholder="Seu E-mail" />
            <textarea rows="1" placeholder="Mensagem" name="message_html" className="message-contact"></textarea>
            <button value="Send">ENVIAR</button>
          </div>
        </div>
      </div>
    </form>
		
  );
}