import React, { Fragment } from 'react';
// reactstrap components
import { Container } from "reactstrap";

// particles
import Particles from 'react-particles-js';

//logo
import logo from '../../assets/img/eyevision/250.png';

// resize
import Media from 'react-media';

// core components

function IndexHeader() {
  return (
    <>

      <div>
        <Media queries={{
          small: "(max-width: 899px)",
          medium: "(min-width: 900px) and (max-width: 1199px)",
          large: "(min-width: 1200px)"
        }}>
          {matches => (
            <Fragment>
              {matches.small && <div
                className="page-header section-dark"
                style={{
                  backgroundColor: "#ffff"
                }}
              >
                <div>
                 <img src={logo} style={{ marginLeft: "50px" }} />
                 <h6 style={{ marginLeft: "20px"}}>A inovação em um simples piscar de olhos</h6>
                </div>
                <div className="filter" />
                <div className="content-center">
                  <Container>
                    <div className="title-brand">
                      {/* <img src={logo} alt="" /> */}
                      <div className="presentation-title">
                        {/* <img alt="..." src={require("assets/img/eyevision/500novo.png")} /> */}
                      </div>
                    </div>
                    <br />
                    {/* <h2 className="presentation-subtitle text-center">
            A inovação em um simples piscar de olhos
            </h2> */}
                  </Container>
                </div>
              </div>}

              {/* MEDIUM */}
              {matches.medium && <div
                className="page-header section-dark"
                style={{
                  backgroundImage:
                    "url(" + require("assets/img/eyevision/fundo_logo.jpg") + ")"
                }}
              >
                <h6 className="presentation-subtitle text-center" style={{ marginBottom: "600px"}}>A inovação em um simples piscar de olhos</h6>
                <div className="filter" />
                <div className="content-center">
                  <Container>
                    <div className="title-brand">
                      {/* <img src={logo} alt="" /> */}
                      <div className="presentation-title">
                        {/* <img alt="..." src={require("assets/img/eyevision/500novo.png")} /> */}
                      </div>
                    </div>
                    <br />
                    {/* <h2 className="presentation-subtitle text-center">
            A inovação em um simples piscar de olhos
            </h2> */}
                  </Container>
                </div>
              </div>}

              {/* LARGE */}
              {matches.large && <div
                className="page-header section-dark"
                style={{
                  backgroundImage:
                    "url(" + require("assets/img/eyevision/fundo_logo.jpg") + ")"
                }}
              >
                <div className="filter" />
                <h6 className="presentation-subtitle text-center" style={{ marginBottom: "600px"}}>A inovação em um simples piscar de olhos</h6>
                <div className="content-center">
                  <Container>
                    <div className="title-brand">
                      {/* <img src={logo} alt="" /> */}
                      <div className="presentation-title">
                        {/* <img alt="..." src={require("assets/img/eyevision/500novo.png")} /> */}
                      </div>
                    </div>
                    <br />
                    {/* <h2 className="presentation-subtitle text-center">
            A inovação em um simples piscar de olhos
            </h2> */}
                  </Container>
                </div>
              </div>}
            </Fragment>
          )}
        </Media>
      </div>


    </>
  );
}

export default IndexHeader;
